import * as React from "react";
import "./App.css";

function App() {
  const [locations, setLocations] = React.useState([]);

  React.useEffect(() => {
    async function fetchIt() {
      let hr = await fetch(
        "https://locations-api-test11.herokuapp.com/api/locations"
      );
      let locationsFromBackend = await hr.json();

      setLocations(locationsFromBackend);
    }
    fetchIt();
  }, []);

  console.log(locations);

  return (
    <div className="App">
      <header className="App-header">
        Locations
        <ul>
          {locations.map((location) => {
            return (
              <li key={location.id}>
                {location.id} - {location.latitude} - {location.longitude}{" "}
              </li>
            );
          })}
        </ul>
      </header>
    </div>
  );
}

export default App;
